* {
    box-sizing: border-box;
    letter-spacing: 1px;
    
}

.loginForm {
    padding: 3vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.loginForm__title{
    margin-bottom: 2vh;
    font-size: 2.5vmax;
}

.loginForm__input{
    font-size: 1.8vmax;
    padding: .2vmax;
}

.loginForm__input, .loginForm__submit{
    margin-bottom: 1vh;
}

.mainButtonContainer{
    padding: 2vh 2vw;
    text-align: center;
}

/* ADDING */

.adding{
    padding: 2vh 2vw;
    text-align: center;
}

.adding__titleText{
    padding: 1vh 2vw;
}

.adding__form{
    display: flex;
    flex-direction: column;
}

.adding__input{
    font-size: 1.8vmax;
    padding: .2vmax;
    margin-bottom: 2vh;
}

.adding__fieldsTitle{
    margin-bottom: 2vh;
}

.adding__fieldsContainer{
    display: flex;
    flex-direction: row;
}

.adding__inputf{
    font-size: 1.8vmax;
    padding: .2vmax;
    flex: 1;
    margin: 1vh 1vw;
}
