
* {
    box-sizing: border-box;
    letter-spacing: 1px;
    margin: 0;
}


body{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size:medium;
    font-size: 2vmax;
}

input{
    font-size: 1.8vmax;
    padding: .2vmax;
    margin-bottom: 2vh;
}

.page{
    margin-top: 10vh;
}

main{
    background-color: rgba(216, 216, 216);
    min-height: 50vh;
}

h2{
    font-size: 2.3vmax;
    padding: 1vh 1vw;
}

button, input[type=button], input[type=submit]{
    background-color: white;
    color: black;
    padding: 1vh 1vw;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 2vmax;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
    border: 2px solid #2368d6;
}

button:hover, input[type=button]:hover, input[type=submit]:hover {
    background-color: #2c7cfd;
    color: white;
}