.banerTop{
    top: 0;
    left: 0;
    position: fixed;
    background-image: linear-gradient(to bottom right, #2368d6, #2c7cfd);
    height:10vh;
    width: 100%;   
    box-shadow: 0 0 4px 2px gray; 
    z-index: 98;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex: 0;
}

.banerTopLogoContainer{
    flex: 1;
    color: antiquewhite;
    font-size: 5vh;
    padding: 0 1vw;
    flex-direction: row;
    height: 10vh;
}

.banerTopLogoContainer img{
    height: 9vh;
}

.banerTopLogoContainer img:hover{
    transition: 0.25s;
    cursor: pointer;
    opacity: 70%;
}

.banerTopLinksContainer{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 1vw;
}

.banerTopLink{
    color: #eee;
    display: flex;
    flex-direction: column;
    padding: 0 2vw;
}

.banerTopLink:hover{
    transition: 0.25s;
    cursor: pointer;
    color: #000;
}

.banerTopIcon{
    font-size: 4.3vh;
}

.banerTopP{
    font-size: 2vh;
}

