.contact{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.contactInfo{
    flex: 1;
    text-align: center;
    background-color: white;
    margin: 2vh 2vw;
    padding: 2vh 2vw;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.contactMail{
    flex: 1;
    margin: 2vh 2vw;
}

.contactMail input{
    margin-top: 1vh;
    width: 100%;
}

.contactMail textarea{
    width: 100%;
    min-height: 10vh;
    font-size: 1.6vmax;
    padding: .2vmax;
}