.PreviewProducts{
    padding-bottom: 3vh;
}

.preview__search{
    margin-top: 1vh;
    padding: 1vh 2vw;
}

.preview__searchInput{
    font-size: 1.8vmax;
    padding: .2vmax;
    width: 100%;
}

.preview__elementContainer{
    margin: 2vh 2vw;
    border: 2px solid black;
    padding: 1vh 1vw;
    display: flex;
    flex-direction: row;
    background-color: white;
}

.previewImg{
    height: 100%;
    margin: 1vh 2vw;
    min-height: 20vh;
    max-width: 40vw;
    object-fit: contain;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.previwe__elementName{
    font-size: 2.5vmax;
}

.previwe__price{
    padding: 1vh 1vw;
    font-size: 2.2vmax;
}

.previwe__available{
    font-size: 1.8vmax;
    color: green;
}

.previwe__parameters{
    padding: 1vh 2vw;
    font-size: 2.2vmax;
}

.preview__fieldValue{
    font-size: 1.6vmax;
}
