 
 .homePage{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0 3vw;
 }

 .homePage p{
    font-weight: bold;
    margin-right: 4vw;
 }

 .homePageBox1,  .homePageBox2,  .homePageBox3{
     display: flex;
     flex-direction: row;
     align-items: center;
     justify-content: center;
 }

 .homePageBox1{
    flex: 100%;
    margin: 1vh 1vw;
    margin-top: 2vh;
    height: 40vh;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
 }

 .homePageBox2{
    flex: 1;
    margin: 1vh 1vw;
    margin-bottom: 5vh;
    height: 40vh;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
 }

 .homePageBox3{
    flex: 1;
    margin: 1vh 1vw;
    margin-bottom: 5vh;
    height: 40vh;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
 }

 .homePageBox1 img {
    height: 30vmin;
 }

 .homePageBox2 img {
    height: 30vmin;
 }

 .homePageBox3 img {
    height: 30vmin;
 }

 @media all and (max-width: 900px) {
    .homePageBox2,  .homePageBox3{
    flex: 100%;
    margin: 1vh 1vw;
    margin-top: 2vh;
    height: 40vh;
    }
    .homePageBox3{
        margin-bottom: 3vh;
    }
}